/* 预约 */
.colorBlock {
	display: inline-block;
	width : 3vw;
	height: 2vw;
	margin-right: 3px;
}

.tagBlock {
	position: absolute;
	right:4vw;
}

.cardText {
	font-size: 1.2em;
	margin: 0 2vw;
}

.cardBlock {
	position: absolute;
  right: 2vw;
  top: 2vw;
  background: #ffaa5f;
  width: 14vw;
  height: 14vw;
  text-align: center;
}

.blockText {
	font-size: 1.2em;
	color: #fff;
	line-height: 14vw;
}

.dayTitle {
	-ms-flex: 1 1;
	    flex: 1 1;
	font-size: 1.2em;
	text-align: center;
	color: rgba(51, 51, 51, 0.73);
}

.dayText {
	font-size: 1.2em;
  line-height: 10.8vw;
}

.dayBlock {
  width: 13vw;
  height: 10.8vw;
}

.tabledItem {
	border-left:0.5px solid #d3d3d3;
  	border-top: 0.5px solid #d3d3d3;
  	text-align: center;
}

/* 记录 */
.record {
	width: 92vw;
	margin: 4vw;
	height: 100%;
}

.record .am-button {
	font-size: 1em;
	width: 20vw;
	height: 8.6vw;
	border-radius: 4.3vw;
	line-height: 8.6vw;
	background: transparent;
	border: 1px solid #474746 !important;
	-ms-flex-item-align: end;
	    align-self: flex-end;
	color: #666666;
}

.modalContent {
	overflow: scroll;
  	background:#fff;
  	padding:4vw;
  	text-align:center;
}

.modalContent .am-list-item{
	padding-left: 0;
}

.modalContent .am-textarea-label{
	color: #888;
	font-size: 1em;
}

.modalContent .am-textarea-control textarea{
	color: #888;
	font-size: 1em;
	text-align: right;
}

.tableBg {
	background: rgba(255,255,255,0.7);
	/*border: 1px solid #333333;*/
	-webkit-box-shadow:0 2px 1px rgba(12, 52, 80, 0.5);
	        box-shadow:0 2px 1px rgba(12, 52, 80, 0.5)
}

.tabledItem .am-button{
	width: 16vw;
	height: 6.6vw;
	border-radius: 3.3vw;
	line-height: 6.6vw;
	color: #fff;
	background: #0c3450;
	border:none !important;
}

.modalTitle {
	color: #000;
	font-size: 1.4em;
	padding-bottom: 2vw;
	border-bottom: 3px solid #0c3450;
}

.modalBtn {
	background: #0c3450;
	width: 100%;
	height: 12vw;
	border-radius: 6vw;
	color: #fff;
	margin-top: 6.8vw;
}

/* 历史 */

.am-modal.am-modal-transparent{
	background: transparent;
	width: 92vw !important;
}

.am-modal.am-modal-transparent.am-modal-content {
	padding: 0 !important;
	border-radius: 0 !important;
}

.am-modal.am-modal-transparent.am-modal-content.am-modal-body{
	padding: 0 !important;
}

.historyModal .am-modal-content{
	background: transparent !important;
}


.modalList {
	height: 12.7vw;
	border-bottom: 1px solid #d3d3d3;
}

.modalText {
	color: #000;
}

.modalText0 {
	color: #888;
}

.cancelBtn {
	width: 18vw;
	font-size: 1em;
  	line-height: 6.6vw;
 	border-radius: 3.3vw;
 	background: #0c3450;
  	color: #fff;
}


.fullBg {
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}



