.inputArea .am-list-body {
	background: transparent;
}

.inputArea .am-list-item .am-input-label {
	font-size: 1.16em;
	color: #999999;
	margin-right: 0;
}

.inputArea .am-list-item .am-input-label.am-input-label-5{
	width: 15.8vw;
}

.inputArea .am-list-item {
	border: 0.5px solid #474746;
	margin-bottom: 4vw;
}

.inputArea .am-input-clear{
	position: absolute;
	right: 10px;
}

.inputArea .am-list-item .am-input-control input{
	font-size: 0.8em;
}

.inputArea .am-button {
	color: #fff;
}

.inputArea html:not([data-scale]) .am-list-body div:not(:last-child) .am-list-line::after{
	height:0;
}

.inputArea html:not([data-scale]) .am-list-body::before{
	height:0;
}

.inputArea html:not([data-scale]) .am-list-body::after{
	height:0;
}


