 body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 12px;
}

@supports (padding-bottom: constant(safe-area-inset-bottom)) { 
  body { padding-bottom: constant(safe-area-inset-bottom); } 
}

div {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

p {
  margin: 0;
}

a { 
  text-decoration:none; 
  color:#333; 
} 

hr {
  margin: 0;
  border: none;
}

button {
  border: none;
  padding: 0;
}


.boxStyle {
  padding:4vw;
}

.hrStyle {
  background: #dad8d8;
  width : 100vw;  
  height: 1.6vh;
  border: none;
  margin: 10px 0;
}

.cardStyle {
  border-radius: 3px;
  -webkit-box-shadow: 0 0 5px 0 #222;
          box-shadow: 0 0 5px 0 #222;
  position: relative;
}

.cardName {
  position: absolute;
  bottom:2.8vw;
  right: 2.8vw;
  color: #fff;
  font-size: 1.2em;
}

.miniBtn {
  background: #efefef;
  display: inline-block;
  width : 18.8vw;
  height: 6vw;
  border-width: 0;
  border-radius: 3vw;
  color: #666666;
  font-size: 1em;
  text-align:center;
  line-height: 6vw;
}

.listText {
  color: #666666;
  font-size: 1.2em;
}

.listDivider {
  height: 0px;
  border-top: 1px #bfbfbf dashed;
}

.tableDivider{
  height: 0px;
  border-top: 1px #707070 solid;
}

.textCard {
  width  : 10.5vw;
  height : 30.5vw;
}

.textContent {
  font-size: 1em;
  width: 4vw;
  display: block;
  margin-top:2vw;
  text-align: center;
}

.btnStyle {
  width : 57vw;
  height: 11.8vw;
  border: 1px solid #aaaaaa;
  margin-bottom: 4vw;
  font-size: 1.2em;
}

.tableLine {
  width:92vw;
  height:1vw;
  background:#333333;
}

.am-checkbox-inner {
  border-radius: 0 !important;
}

.am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border: none;
  background: #ec6941;
}

.am-checkbox-inner:after {
  border-width: 0 3px 2px 0;
}

.textTitle{
  overflow: hidden;
  -o-text-overflow:ellipsis;
     text-overflow:ellipsis;
  white-space: nowrap;
}

.htmlContent img {
  max-width: 100%
}




