.bottom {
	position: fixed;
	bottom: 0;
	width : 100vw;
	height: calc(14vw + constant(safe-area-inset-bottom));
}

.bottomBtn{
	border: none;
	border-radius: 0;
	height: 14vw;
	-ms-flex: 1 1;
	    flex: 1 1;
	font-size: 1.37em;
	background: #fff;
}

.confirm{
	background: #ec6941 !important;
	color: #fff !important;
}

.page .am-list-item {
	-ms-flex: 1 1;
	    flex: 1 1;
	padding-left: 0;
}

.flexDivDate {
	-ms-flex: 1 1;
	    flex: 1 1;
}

/* 列表文字大小 */
.page .am-list-item .am-input-label {
	font-size: 1.2em;
}

.page .am-list-item .am-list-line .am-list-content {
	font-size: 1.2em;
}

.page .am-list-item .am-list-line .am-list-extra {
	font-size: 1.2em;
	-ms-flex-preferred-size: 50%;
	    flex-basis: 50%;
}

/* DatePicker 取消右侧符号 */
.page .am-list-item .am-list-line .am-list-arrow-horizontal{
	width: 0;
	margin-left: 0;
}

/* DatePicker 选中文字大小 */
.flexDivDate .am-list-item .am-list-line .am-list-extra {
	font-size: 1.2em;
	text-align: left;
}

.flexDivDate .am-list-item .am-list-line{
	padding-right: 0;
}


/*列表文字*/

.page .am-flexbox .am-flexbox-item{
	min-width: 100px;
	min-height: 100px;
}

.am-list-item .am-input-control input{
	font-size: 1em;
	text-align: right;
}

.step3 .am-list-item{
	border-bottom: 1px solid #ddd;
}

.page .step4 .am-list-item{
	-ms-flex: 0.5 1;
	    flex: 0.5 1;
}

.page4 .am-list-item .am-list-line{
	padding-left: 4vw;
}




.step3 .am-list-body .am-list-item:last-child{
  border-bottom: 1px solid #ddd !important;
}

html:not([data-scale]) .step3 .am-list-body div:not(:last-child) .am-list-line::after{
	height: 0;
}

html:not([data-scale]) .step4 .am-list-body::before{
	height: 0;
}

html:not([data-scale]) .step4 .am-list-body div:not(:last-child) .am-list-line::after{
	height: 0;
}

html:not([data-scale]) .step4 .am-list-body::after{
	height: 0;
}

html:not([data-scale]) .step5 .am-list-body::before{
	height: 0;
}

html:not([data-scale]) .step5 .am-list-body div:not(:last-child) .am-list-line::after{
	height: 0;
}

html:not([data-scale]) .step5 .am-list-body::after{
	height: 0;
}



