.tabs .am-tabs-default-bar-content{
  background: #EEEEEE;
  height:12vw;
  -ms-flex-align: end;
      align-items: flex-end;
}

.tabs .am-tabs-default-bar-tab{
  background: #EEEEEE;
  margin: 0 3vw;
  width: 44vw !important;
  height: 10vw;
  color: rgba(51, 51, 51, 0.59);
  font-size:1.2em;
}

.tabs .am-tabs-default-bar-tab-active{
  background: #fff;
  color: #333333;
}

.tabs .am-tabs-default-bar-underline{
  border: 2px solid #333333;
  border-radius: 2px;
  width: 20% !important;
  margin-left: 15%;
}

html:not([data-scale]) .am-tabs-default-bar-top .am-tabs-default-bar-tab::after{
  height: 0px;
}

.article {
  width  : 100vw;
  min-height: 58vh;
  padding: 4vw;
  background:#fff;
  padding-top: 4vw;
}


